/* eslint-disable */

import React, { useContext, useEffect, useState } from 'react';
import { Navbar,Nav,NavDropdown,Container,Form,Button,FormControl } from 'react-bootstrap';
import { useHistory,useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
import {재고context} from './App.js';
import {CSSTransition} from "react-transition-group";

let 박스 = styled.div`
    padding:20px;
`;
let 제목 =styled.h4`
    font-size:25px;
    color:${ props => props.색상 }
`;

class Detail2 extends React.Component{
  componentDidMount(){
    // 컴포넌트가 mount되었을때(보일때)실행
  }
  componentWillUnmount(){
    // 컴포넌트가 Unmount되었을때(사라질때)실행
  }

}



function Detail (props) {

    let [alert, alert변경] = useState(true);
    let 재고 = useContext(재고context);
    let [누른탭, 누른탭변경] = useState(0);
    let [스위치, 스위치변경] = useState(false);
  

    useEffect(()=>{
      // 컴포넌트가 mount되었을때(보일때), update될때 실행
     let 타이머 =  setTimeout(()=>{ alert변경(false) },2000);
     console.log('안녕');
     return ()=>{clearTimeout(타이머)}
     //return  컴퍼넌트가 사라질때 실행
    },[alert]);
    //[]안은 업데이트될때 조건 useEffect()의 조건부 실행 alert,inputData []빈것이면 로드될때만 뜨게됨

    
    let [inputData, inputData변경] = useState('');

    let { id } = useParams();
    let 찾는상품 = props.shoes.find(function (상품) {
        return 상품.id == id
    })
    let history = useHistory();

    return(
      <div className="container">
          <박스>
            <제목 색상='blue' >상세페이지</제목>
          </박스>
          <박스>
            <제목 className='red' >상세페이지</제목>
          </박스>
         
         {inputData}
         <input onChange={(e)=>{ inputData변경(e.target.value)}}/>

         {
            alert === true
            ?(<div className="my-alert2">
              <p>재고가 얼마 남지 않았습니다</p>
            </div>)
            : null
          }
          
          <div className="row">
            <div className="col-md-6">
              <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{찾는상품.title}</h4>
              <p>{찾는상품.content}</p>
              <p>{찾는상품.price}</p>
              <Info 재고={props.재고}></Info>
              <button className="btn btn-danger" onClick={()=>{ props.재고변경([9,11,12])}}>주문하기</button> 
              <button className="btn btn-danger" onClick={()=>{
                history.goBack();
              }}>뒤로가기</button>
              <button className="btn btn-danger" onClick={()=>{
                history.push('/sdft');
              }}>이동하기</button>
            </div>
          </div>
          
          <Nav className="mt-5" variant="tabs" defaultActiveKey="/link-0">
            <Nav.Item>
              <Nav.Link eventKey="link-0" onClick={()=>{ 스위치변경(false); 누른탭변경(0) }}>Active</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-1" onClick={()=>{ 스위치변경(false); 누른탭변경(1) }}>Option 2</Nav.Link>
            </Nav.Item>
          </Nav>
          
          <CSSTransition in={스위치} classNames="wow" timeout={50000}>
            <TabContent 누른탭={누른탭} 스위치변경={스위치변경}></TabContent>
          </CSSTransition>
          
          


        </div> 
    )
  }

  function TabContent(props) {
    
    useEffect( ()=>{
      props.스위치변경(true);
    });

    if(props.누른탭 === 0){
      return <div>0000페이지</div>
    }else if(props.누른탭 === 1){
      return <div>1111페이지</div>
    }else if(props.누른탭 === 2){
      return <div>2222페이지</div>
    }
  }




  function Info(props) {
    return(
      <p>재고 : {props.재고[0]} </p>
    )
  }


  export default Detail;